@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1240px;
  }
}
#slider-topo {
  margin-bottom: 0;
  width: 100%;
}
#slider-topo .slick-prev {
  display: none !important;
}
#slider-topo .slider-item img,
#slider-topo .slider-item video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  object-position: top;
}
#slider-topo .container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#slider-topo .container .slider-text {
  width: 30%;
}
#slider-topo .container .slider-text h2 {
  font-size: 30px;
  line-height: 37px;
  font-weight: bold;
  color: #cae098;
}
#slider-topo .container .slider-text h2::first-line,
#slider-topo .container .slider-text p {
  line-height: 20px;
  color: #fff;
}
#slider-topo .container .slider-text p {
  margin-bottom: 0;
}
#slider-topo .container a {
  margin-top: 20px;
}
#slider-topo .slick-dots {
  bottom: 5%;
  right: 5%;
  width: max-content;
}
#slider-topo .slick-dots li {
  margin: 0;
}
#slider-topo .slick-dots li button:before {
  content: "";
  background: #fff;
  color: #fff;
  transition: all ease 0.3s;
  border: 2px solid #fff;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  opacity: 1;
}
#slider-topo .slick-dots li.slick-active button:before {
  border-color: #a4c659;
}

#nossos-servicos {
  background: #d9d9d9;
  padding: 80px 0;
}
#nossos-servicos .title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  border-left: 5px solid #006635;
  padding-left: 50px;
  height: 90px;
}
#nossos-servicos .title span {
  color: #006635;
}
#nossos-servicos .title p {
  font-weight: 500;
  margin-bottom: 0;
}
#nossos-servicos #slider-nossos-servicos {
  padding-top: 60px;
  overflow: hidden;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos {
  flex-wrap: nowrap;
  gap: 15px;
  padding-left: 15px;
  padding-right: 100px;
  overflow: auto;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 0;
  border-radius: 5px;
  width: 450px;
  cursor: pointer;
  transition: all ease 0.3s;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .left-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  writing-mode: sideways-lr;
  text-orientation: mixed;
  line-height: 1.1;
  white-space: nowrap;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .left-title h3 {
  color: #dbdbdb;
  opacity: 0.35;
  font-weight: bold;
  padding: 15px 0;
  margin: 0;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  padding-bottom: 25px;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item img {
  max-width: 100%;
  border-radius: 0 5px 0 0;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item h3 {
  font-size: 20px;
  font-weight: bold;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item h4 {
  font-size: 16px;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item .saiba-mais {
  width: max-content;
  min-width: 140px;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item .saiba-mais:hover {
  background-color: #000;
  color: #fff;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .left-title,
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .saiba-mais, #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .left-title,
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .saiba-mais {
  display: none;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .content-item, #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .content-item {
  position: relative;
  width: 100%;
  padding-bottom: 0;
  height: 400px;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .content-item img, #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .content-item img {
  border-radius: 5px;
  height: 100%;
  object-fit: cover;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .content-item .titles, #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .content-item .titles {
  position: absolute;
  left: 5%;
  bottom: 10%;
  color: #fff;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active {
  width: 60%;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive {
  width: 25%;
}
#nossos-servicos .content-servico h2,
#nossos-servicos .content-servico h3 {
  font-size: 16px;
}
#nossos-servicos .content-servico .subservicos-wrapper {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 12.5%;
  gap: 60px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
}
#nossos-servicos .content-servico .subservicos-wrapper .slick-prev {
  left: 0;
}
#nossos-servicos .content-servico .subservicos-wrapper .slick-next {
  right: 0;
}
#nossos-servicos .content-servico .subservicos-wrapper .subservico-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 15px;
}
#nossos-servicos .content-servico .subservicos-wrapper .subservico-item h3 {
  margin-bottom: 0;
}